.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.card {
  max-width: 400px;
  width: 100%;

  &:hover .card-content{
    transform: rotateY(180deg);
  }

  .card-content {
    position: relative;
    padding: 15rem 5rem;
    transform-style: preserve-3d;
    transition: transform 3s;
  }

  .card-front,
  .card-back {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    padding: 2rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    backface-visibility: hidden;
    transform-style: preserve-3d;
  }

  .card-front,
  .card-back {
    background-size: cover;
    background-repeat: no-repeat;
    background-blend-mode: overlay;
    /*color: white;*/
  }

  .card-front {
    background-image: url("./img/card-front.jpeg");
    background-color: rgba(255, 163, 42, 0.2);
    align-items: center;
    z-index: 2;

    &::before {
      content: "";
      position: absolute;
      inset: 1rem;
      border: 3px solid yellow;
      transform: translateZ(2rem);
    }
  }

  .card-back {
    background-image: url("./img/card-back.jpeg");
    transform: rotateY(180deg);
    gap: 0.5rem;
    z-index: 1;
  }

  .title {
    font-size: 4.5rem;
    line-height: 120%;
    text-transform: uppercase;
    transform: translateZ(4.4rem);
  }

  .sub-title {
    font-size: 0.75rem;
    font-weight: 500;
    letter-spacing: 3px;
    text-transform: uppercase;
    transform: translateZ(3rem);

  }
}




